.nav-container{

    border-radius: 40px;
}
.card-title{
    text-align: left;
}
.card-text{
    text-align: left;
}
.btn-primary{
    text-align: center;
    align-items: center;
    align-self: center;
}
.btn{
    align-self: center;
}
.card{
    background: linear-gradient(-45deg, #24ff72, #9a4eff);
    border-radius: 15px;
    
}
.card:hover {
    box-shadow: 5px 10px #262ff2, 5px 10px #262ff2;
  }

  .card a {
    display: block;
    color: black;
    position: relative;
    z-index: 1;
    background-color: #fff;
    color: black;
    width: 150px;
    height: 40px;
    text-align: center;
   
    margin: 20px auto 0;
    line-height: 40px;
    border-radius: 40px;
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    box-shadow: 0 5px 10px rgba(0,0,0, .1);
  }
  .align-right{
    float: right;
    position: fixed;

  }
  .container-loader{
    margin: auto;
    width: 10%;
    color: blanchedalmond;
    margin-top: 50px;
  }

  ul.dropdown-menu {
    max-height: 500px;
    overflow-y: scroll;
}
.div-ordered-list{
    display: inline;
}
.ul-ordered-list li{
    display: inline;
    float: left;
}